.page {
  height: 100%;
  padding: 1rem;
  overflow-x: hidden;
  overflow-y: auto;

  &--bgWhite {
    background: rgba(255, 255, 255, .8);
    box-shadow: 0 1px 33px rgba(0, 0, 0, 0.15);

    &::before {
      content: '';
      border-style: solid;
      border-color: var(--primary);
      border-width: 5px 0 0 5px;
      height: 80px;
      width: 80px;
      display: block;
    }
  }

  &__title {
    text-align: center;
    margin: -4rem 0 2rem;
    padding: 0;
    font-size: 5rem;
    color: $gray-400;

    & > span {
      font-weight: 700;
    }
  }

  &__subTitle {
    display: block;
    color: $light;
    font-size: 16px;

    span {
      background-color: var(--primary);
      display: inline-block;
      padding: .125rem .25rem;
    }

    span:nth-child(n+2) {
      margin-left: .5rem;
    }
  }

  &__section:nth-of-type(n+2) {
    margin-top: 3rem;
  }

  &__heading {
    border-left: 3px solid $primary;
    margin-bottom: 1rem;
    padding-left: 1rem;
    font-weight: 400;
  }

  &__transition {
    &Definition {
      transition-property: opacity, transform;
      transition-duration: $pageTransitionDuration;
      transition-timing-function: ease-out;
    }

    &-enter {
      opacity: 0;
      transform: scale(.5, .5);
    }

    &-enter-active {
      @extend .page__transitionDefinition;
      opacity: 1;
      transform: scale(1, 1);
    }

    &-exit {
      opacity: 1;
      transform: scale(1, 1);
    }

    &-exit-active {
      @extend .page__transitionDefinition;
      opacity: 0;
      transform: scale(.5, .5);
    }
  }

  &__container {
    position: relative;
    height: 100%;
  }
}
