.pageAbout {
  &__spec {
    dl {
      .dt, .dd {
        line-height: 32px;
      }

      .dt {
        border-left: 2px solid $primary;
        padding-left: 1rem;
      }

      .dd {
        margin: 0;
      }
    }
  }

  dl, dt, dd {
    margin-bottom: 0;
  }

  dl:nth-of-type(n+2) {
    margin-top: .5rem;
  }

  .card {
    text-align: center;
    padding-top: 1.25rem;

    &-body {
      text-align: center;
    }

    &-img-top {
      font-size: 50px;
      margin: 0 auto 1rem;
    }
  }

  &__sectionService {
    .card-title {
      font-size: 1rem;
      font-weight: 700;
    }

    .card-wrapper {
      &:nth-child(1) {
        .card-img-top {
          color: var(--success)
        }
      }

      &:nth-child(2) {
        .card-img-top {
          color: var(--danger)
        }
      }

      &:nth-child(3) {
        .card-img-top {
          color: var(--warning)
        }
      }
    }
  }

  &__sectionFunFact {
    .card-text {
      color: var(--primary);
    }

    .card-wrapper {
      &:nth-child(1) {
        .card-img-top {
          color: var(--indigo)
        }
      }

      &:nth-child(2) {
        .card-img-top {
          color: var(--pink)
        }
      }

      &:nth-child(3) {
        .card-img-top {
          color: var(--orange)
        }
      }

      &:nth-child(4) {
        .card-img-top {
          color: var(--teal)
        }
      }
    }
  }
}