.pageHome {
  display: table;
  width: 100%;
  text-align: center;

  &__block {
    display: table-cell;
    vertical-align: middle;

    &::after {
      content: ' ';
      clear: both;
    }
  }

  &__name {
    font-size: 70px;
    font-weight: 700;
    color: var(--primary);
  }

  &__jobTitle {
    font-size: 16px;
  }
}
