@include media-breakpoint-down(sm) {
  .pageHome {
    &__name {
      font-size: 40px;
    }
  }

  .page {
    &__title {
      font-size: 2rem;
      padding: 0 .5rem;
    }

    &__subTitle {
      margin-top: .25rem;

      span {
        font-size: 14px;
      }
    }
  }

  .navigation {
    &__mainNav {
      padding-left: 15px;
    }
  }
}

