.pageResume {
  &__timeline {
    &Item {
      border-left: 1px solid $gray-200;
      padding: 0 0 1rem 1.5rem;
    }

    &Time {
      display: inline-block;
      margin-left: calc(-1.5rem - 1px);
      padding: .125rem 1rem;
      background: var(--primary);
      color: var(--light);
    }

    &Title {
      margin-top: 1rem;
      font-weight: 700;
    }

    &SubTitle {
      color: $gray-600;
      margin-bottom: 1rem;
      font-size: 12px;
    }

    &Desc {
      margin: 0;
      padding-inline-start: 1rem;
      font-size: 80%;
    }
  }

  &__skill {
    &Item {
      margin-bottom: 1rem;
    }

    &Title {
      font-weight: 700;
      font-size: 12px;
    }

    &Progress {
      height: .5rem;
      border-radius: 0;
    }
  }
}