.content {
  margin: 0;
  background: url("/img/bg.png") no-repeat center center fixed;
  height: calc(100vh - #{$navigationHeight});
  height: -webkit-calc(100% - #{$navigationHeight});

  &__alert {
    .alert {
      margin: 1rem 0 0;
    }
  }

  &__fluid {
    padding-top: 1rem;
  }

  &__page {
    padding: 1rem 0;
  }

  &__title {
    padding-bottom: 1rem;
  }
}
