.pagePdf {
  &__container {
    background: #fff;
    box-shadow: 0 0 6px #ccc;
  }

  &__title {
    line-height: 46px;
    border-bottom: 6px solid #222;
    padding: 50px 0;
    margin: 0 0 30px 0;
  }

  &__section {
    &:nth-child(n+3) {
      border-top: 1px dashed $gray-300;
      margin-top: 2rem;
      padding-top: 2rem;
    }

    &:after {
      content: "";
      clear: both;
    }

    & ul {
      list-style-type: square;
    }

    .card {
      border: 0;

      &-body {
        padding: 1rem 0;

        .table {
          border: 1px solid $gray-300;

          th, td {
            border: 1px solid $gray-300;
          }
        }
      }
    }
  }

  &__subTitle {
    font-size: 32px;
    line-height: 32px;
    font-weight: 700;
  }

  &__contact {
    p {
      &:last-of-type {
        margin-bottom: 0;
      }

      label {
        margin: 0 .5rem 0 0;
      }
    }
  }

  &__work {
    &CompanyName, &CompanyPosition, &Date {
      display: inline-block;
    }

    &Date {
      font-size: 16px;
    }

    &CompanyPosition {
      small {
        font-size: 70%;
      }
    }

    & {
      .carousel {
        .card-body {
          min-height: 220px;
          padding: 0;
        }
      }
    }

    &CompanyName {
      font-size: 18px;
      font-weight: 700;

      &::after {
        content: "—";
        margin: 0 1rem;
        font-weight: normal;
      }
    }
  }

  &__project {
    &ContentItem {
      &:nth-child(n+2) {
        margin-top: 1rem;
      }

      .card-body {
        padding: 0;
      }
    }
  }
}
