.navigation {
  position: fixed;
  top: auto;
  bottom: 0;
  z-index: 100;
  padding: 0;
  height: $navigationHeight;
  width: 100%;
  background: rgba(255, 255, 255, .8);
  box-shadow: 0 1px 10px rgba(0, 0, 0, .2);

  &__mainNav {
    height: $navigationHeight;
  }

  &__photo {
    float: left;
    height: 60px;
    width: 60px;
    margin: 10px 10px 0 0;
  }

  &__name {
    color: var(--primary);
    line-height: $navigationHeight;
    font-weight: 400;
    font-size: 24px;
    margin: 0;
    padding: 0;
  }

  &__item {
    position: relative;
    width: calc(100% / 3);
    padding-top: 1rem;
  }

  &__link {
    color: #555;
    font-weight: 400;
    padding: 0 1rem;
    text-align: center;
    height: $navigationHeight;
    width: 100%;

    &:before {
      content: '';
      width: 0;
      height: 3px;
      position: absolute;
      top: 0;
      left: 50%;
      background: $primary;
      transform: translateX(-50%);
      transition: width .2s ease-out;
      display: block;
    }

    &:hover, &.active {
      &:before {
        width: 100%;
      }

      color: var(--primary);

      .navigation__title {
        color: #222;
      }
    }
  }

  &__icon, &__title {
    display: block;
  }

  &__icon {
    color: var(--primary);
    height: 24px;
  }

  &__title {
    font-weight: 400;
  }
}
